<template>
  <div id="home">
    <el-container class="home-container">
      <!--头部-->
      <el-header class="home-header" style="height: 65px;">
        <span class="home_title">颐和养老综合管理系统</span>
        <div style="display: flex;align-items: center;margin-right: 7px">
          <el-dropdown @command="handleCommand">
              <span class="el-dropdown-link home_userinfo" style="display: flex;align-items: center">
                {{user.name}}
                <i><img v-if="user.userface!=''" :src="user.userface"
                        style="width: 40px;height: 40px;margin-right: 5px;margin-left: 5px;border-radius: 40px"/></i>
              </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="personalCenter" >个人中心</el-dropdown-item>
              <el-dropdown-item command="setting">设置</el-dropdown-item>
              <el-dropdown-item command="logout" divided>注销</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>

      <el-container>
        <el-aside class="home-aside">
          <div style="display: flex;justify-content: flex-start;text-align: left;">
            <!--侧导航-->
            <el-menu  unique-opened router :collapse="isCollapse">

              <template v-for="(item,index) in this.routes" v-if="!item.hidden">
                <el-submenu  :key="index" :index="index+''">
                  <template slot="title">
                    <i  :class="item.iconCls" style="color: #cd853c;width: 14px;"></i>
                    <span slot="title">{{item.menuName}}</span>
                  </template>
                  <el-menu-item background-color="#eddfc7"
                                style="padding-left: 30px;padding-right:0px;margin-left: 0px;text-align: left;"
                                v-for="(child,is) in item.children"
                                :index="child.component"
                                :key="is+'key'">
                    <i  :class="child.iconCls" style="color: #cd853c;width: 14px;"></i>
                    {{child.menuName}}
                  </el-menu-item>
                </el-submenu>
              </template>
              <!--<el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span>系统管理</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="sysUser">用户管理</el-menu-item>
                  <el-menu-item index="role">权限管理</el-menu-item>
                  <el-menu-item index="menu">菜单管理</el-menu-item>
                </el-menu-item-group>
              </el-submenu>-->
            </el-menu>
            <div class="logo"><img class="yihe" src="../assets/yihe.png"/></div>
          </div>
        </el-aside>

        <el-container>
         <!--内容导航-->
         <el-main>
          <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item ><i class="fa fa-bars fa-lg" aria-hidden="true" @click="closeBar"></i></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <span  class="el-breadcrumb__item" aria-current="page" v-if="!Array.isArray(this.$router.currentRoute.name)">
                  <router-link style="text-decoration: none" :to="{path:this.$router.currentRoute.path}" class="el-breadcrumb__inner">{{this.$router.currentRoute.name}}</router-link>
                <i class="el-breadcrumb__separator el-icon-arrow-right"></i>
              </span>
            <span   v-else-if="Array.isArray(this.$router.currentRoute.name)" class="el-breadcrumb__item " aria-current="page"
                    v-for="val in this.$router.currentRoute.name">
                <span data-id="" @click="gotoLocalRouter(val.path)" class="el-breadcrumb__inner routerRender">{{val.name}}</span>
                <i class="el-breadcrumb__separator el-icon-arrow-right"></i>
              </span>
          </el-breadcrumb>
          <keep-alive>
            <router-view v-if="this.$route.meta.keepAlive" @changRouter="queryGet"></router-view>
          </keep-alive>
          <router-view v-if="!this.$route.meta.keepAlive" @changRouter="queryGet"></router-view>
          <div class="footer">
            <img class="hehua" src="../assets/main_bottom.png"/>
          </div>
        </el-main>
        </el-container>

      </el-container>
    </el-container>
  </div>
</template>

<script>
    export default {
        name: "home",
      data() {
          return{
            isCollapse: false
          }
      },
      methods:{
        handleCommand(cmd){
          switch(cmd) {
            case 'logout':
              this.logout();
              break;
            case "personalCenter":
              this.$router.push({path: '/personalCenter'}); // 对象
              break;
            case "setting":
              this.$router.push({path: '/setting'}); // 对象
              break;
          }
        },
        logout:function (event) {
          this.$confirm('注销登录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http({
              method:'get',
              url:'/logout'
            });
            this.$store.commit('logout');
            this.$router.push({path: '/'}); // 对象
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '取消'
            });
          });
        },
        //关闭bar
        closeBar(){//关闭和打开bar的时候，进行变化
          if(this.isCollapse){
            this.isCollapse = false
            this.$refs.Navcontains.style.width = "auto"
          }else{
            this.isCollapse = true;
            this.$refs.Navcontains.style.width = "55px"
          }
        },
        gotoLocalRouter(path,query){
          let querys = this.routerQuery;
          this.$router.push({path:path,query:querys});
        },
        //通过组件通讯，改变路由query的值，并改变跳转的参数-----2018.3.9
        queryGet(nestStatus){
          // console.log("nestStatus",nestStatus)
          this.routerQuery = nestStatus
        }
      },
      computed:{
        user(){
          return this.$store.state.user;
        },
        routes(){
          //console.log(this.$store.state.routes);
          return this.$store.state.routes;
        },
      }
    }
</script>

<style lang="less">
  #home{
    .el-submenu__title:hover {
      background-color: #eddfc7;
    }
    .el-menu {
      background-color: #eddfc7;
    }

    .home-aside::-webkit-scrollbar{//隐藏滚动条，可以滚动
      display:none;
    }
    .home-aside{
      width:auto !important;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
      width: 200px;
      min-height: 400px;
    }
    .el-menu {//最大宽度为170px
      max-width: 170px;
      .el-menu--collapse{
        width:auto;
      }
    }
    .el-submenu {//最大宽度为170px
      background-image: url("../assets/leftkuang6.png");
      min-width: 170px;
      max-width: 170px;
      .el-menu-item {
        width: auto;

      }
    }

    .el-menu-item.is-active {
      color: #cd853c;
      background-color: #fff;
    }
    .el-menu-item:focus, .el-menu-item:hover {
      background-color: #fff;
    }
    .home-container {
      height: 100%;
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
    }

    .home-header {
      background-image: url("../assets/wuyan.png");
      background-color: #461807;
      color: #fff;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-sizing: content-box;
      padding: 0px;
    }

    .home-aside {
      background-color:#eddfc7;
    }
    .home-main {
      background-color: #fff;
      color: #000;
      text-align: center;
      margin: 0px;
      padding: 0px;;
    }

    .home_title {
      font-weight: 700;
      color: #fff;
      font-size: 22px;
      display: inline;
      margin-left: 8px;
    }

    .home_userinfo {
      color: #fff;
      cursor: pointer;
    }

    .home_userinfoContainer {
      display: inline;
      margin-right: 20px;
    }

    .footer .hehua {
      width: 80%;
      opacity: 0.3;
      position: absolute;
      bottom: 0;
      left: 15%;
      pointer-events: none;
    }
    .logo .yihe {
      width: 160px;
      opacity: 0.2;
      position: absolute;
      bottom: 0;
      left: 0;
      pointer-events: none;
    }
  }
</style>



